#about {
    display: grid;
    grid-template-columns: auto 1fr;
    gap: .5rem;
}

#profile-pic {
    border-radius: 50%;
    width: 35vw;
    margin: .5rem;
    border: 1px solid var(--dark-accent);
}

#profile-text h2 {
    margin: 1.5rem 0 .25rem 0;
    padding-right: .25rem;
    border-bottom: 1px solid var(--dark-accent);
    width: fit-content;
}

#bio {
    grid-column: span 2;
    display: flex;
    flex-direction: column;
    gap: 1rem;
    padding: 0 .75rem;
    font-family: 'Raleway', sans-serif;
}

@media screen and (min-width: 768px) {
    #about {
        padding: 0 10vw;
        font-size: 1.25rem;
    }

    #profile-pic {
        width: 25vw;
    }
}